export default {
	mounted() {
		
	},
	data() {
		return {
			goodsList: [],
			loadingText: "正在加载...",
			headerTop: "0px",
			headerPosition: "fixed",
			orderbyList: [{
					text: "综合",
					selected: true,
					orderbyicon: false,
					orderby: 0
				},
				{
					text: "销量",
					selected: false,
					orderbyicon: ['sheng', 'jiang'],
					orderby: 0
				},
				{
					text: "价格",
					selected: false,
					orderbyicon: ['sheng', 'jiang'],
					orderby: 0
				}
			],
			orderby: "sheng",
			id: "",
			key1: "",
			cur_page: 0,
			tol_page: 1,
			tip: false, //提示默认消失
			hot:"",
			limit: "",
			keyword:""
		}
	},
	onLoad: function(option) {
		console.log("option", option)
		if (option.hot == "hot") {
			//主页数据
			// 
					this.sortType(1,1,1)

			this.hot = option.hot
			uni.setNavigationBarTitle({
				title: "热销产品"
			})
		} else if (option.limit == "limit") {

			this.sortType(1,1,2)
			this.limit = option.limit

			uni.setNavigationBarTitle({
				title: "限时抢购产品"
			})
		} else if (option.keyword) {
			this.keyword = option.keyword;
			
			this.request({
				url: this.config.url + "goods/lists",
				method: "post",
				data: {
					sort: 1,
					asc: 1,
					keyword: this.keyword
				},
				success: (res) => {
					console.log("key", res)
					this.goodsList = res.data.data.data;
				}
			})
		} else if(option.id){
			uni.setNavigationBarTitle({
				title: option.title
			});
			this.key1 = option.id;
			this.fardis(1)
			//兼容H5下排序栏位置










		}


	},

	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		this.cur_page = this.cur_page + 1;

		this.tip = true;

		if (this.cur_page >= this.tol_page) {
			this.loadingText = "没有商品了";
			return false;
		} else {
			this.loadingText = "正在加载...";
		}
	},
	methods: {
		//分类
		fardis(type, asc = 1){
			this.request({
				url: this.config.url + "goods/lists",
				data: {
					token: this.token,
					cate_id: this.key1,
					sort: type,
					asc: asc,
					page: this.cur_page
				},
				method: "post",
				success: (res) => {
			
					this.goodsList = res.data.data.data;
					this.cur_page = res.data.data.current_page;
					this.tol_page = res.data.data.last_page;
				}
			})
		},
		//关键字排序
		word(type, asc = 1, keyword) {
			
				this.tip = false
				this.request({
					url: this.config.url + "goods/lists",
					method: "post",
					data: {
						sort: 1,
						asc: 1,
						keyword: keyword
					},
					success: (res) => {
						console.log("key", res)
						this.goodsList = res.data.data.data;
						this.cur_page = res.data.data.current_page;
						this.tol_page = res.data.data.last_page;
					}
				})
			
		},

		// 排序方式
		sortType(type, asc = 1, action = 0) {
			this.tip = false;
			this.request({
				url: this.config.url + "goods/lists",
				data: {
					token: this.token,
					action: action,
					// cate_id:this.key1,
					sort: type,
					asc: asc,
					page: this.cur_page
				},
				method: "post",
				success: (res) => {
					console.log(res.data.data);
					this.goodsList = res.data.data.data;
					this.cur_page = res.data.data.current_page;
					this.tol_page = res.data.data.last_page;
				}
			})
		},
		reload() {

			this.sortType(1);

		},
		//商品跳转
		toGoods(e) {
			console.log(e)
			var id = e.id;
			uni.navigateTo({
				url: "/pages/goods/goods?id=" + id
			})
		},
		//排序类型
		select(index) {

			let tmpTis = this.orderbyList[index].text + "排序 "
			if (this.orderbyList[index].orderbyicon) {
				let type = this.orderbyList[index].orderby == 0 ? '升序' : '降序';
				if (this.orderbyList[index].selected) {
					type = this.orderbyList[index].orderby == 0 ? '降序' : '升序';
					this.orderbyList[index].orderby = this.orderbyList[index].orderby == 0 ? 1 : 0;
				}
				tmpTis += type
			}
			this.orderbyList[index].selected = true;
			let len = this.orderbyList.length;
			for (let i = 0; i < len; i++) {
				if (i != index) {
					this.orderbyList[i].selected = false;
				}
			}

			if (this.hot) {

				switch (index) {
					case 0:
						this.sortType(1, 1, 1);
						break;
					case 1:
						if (this.orderbyList[index].orderby) {
							this.sortType(2, 2, 1);
						} else {
							this.sortType(2, 1, 1, );
						}
						break;
					case 2:
						if (this.orderbyList[index].orderby) {
							this.sortType(3, 2, 1);
						} else {
							this.sortType(3, 1, 1);
						}
						break;
				}

			} else if (this.limit) {
				switch (index) {
					case 0:
						this.sortType(1, 1, 2);
						break;
					case 1:
						if (this.orderbyList[index].orderby) {
							this.sortType(2, 2, 2);
						} else {
							this.sortType(2, 1, 2);
						}
						break;
					case 2:
						if (this.orderbyList[index].orderby) {
							this.sortType(3, 2, 2);
						} else {
							this.sortType(3, 1, 2);
						}
						break;
				}
			} else if (this.keyword) {
				switch (index) {
					case 0:
						this.word(1, 1, this.keyword);
						break;
					case 1:
						if (this.orderbyList[index].orderby) {
							this.word(2, 2, this.keyword);
						} else {
							this.word(2, 1, this.keyword);
						}
						break;
					case 2:
						if (this.orderbyList[index].orderby) {
							this.word(3, 2, this.keyword);
						} else {
							this.word(3, 1, this.keyword);
						}
						break;
				}
			} else if(this.key1){
				switch (index) {
					case 0:
						this.fardis(1);
						break;
					case 1:
						if (this.orderbyList[index].orderby) {
							this.fardis(2, 2);
						} else {
							this.fardis(2);
						}
						break;
					case 2:
						if (this.orderbyList[index].orderby) {
							this.fardis(3, 2);
						} else {
							this.fardis(3);
						}
						break;
				}
			}


		}
	}

}
